@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.Globals-noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none; }
  .Globals-noselect:focus, .Globals-noselect:active {
    outline: 0; }

.Globals-clickable:hover {
  cursor: pointer; }

.Globals-clickable:focus, .Globals-clickable:active {
  outline: 0; }

.Humanlapse .pageLayout {
  display: grid;
  align-items: stretch;
  justify-items: stretch;
  animation: all 0.5s ease-in-out;
  min-height: 100vh; }
  @media (max-width: 61em) {
    .Humanlapse .pageLayout {
      grid-template-areas: "header" "visuals" "description"; } }
  @media (min-width: 61em) {
    .Humanlapse .pageLayout {
      grid-template-areas: "header visuals" "description visuals"; } }
  .Humanlapse .pageLayoutVisuals {
    grid-area: visuals;
    place-self: center; }
  .Humanlapse .pageLayoutDescription {
    grid-area: description;
    place-self: stretch; }
    @media (min-width: 61em) {
      .Humanlapse .pageLayoutDescription {
        width: 25rem; } }
  .Humanlapse .pageLayoutHeader {
    grid-area: header;
    place-self: flex-stretch; }
    @media (min-width: 61em) {
      .Humanlapse .pageLayoutHeader {
        width: 25rem; } }
  @media (min-width: 61em) {
    .Humanlapse .pageLayoutManualLayoutVideoPlayer {
      height: 95vh;
      width: calc(100vw - 25rem); } }
  @media (max-width: 61em) {
    .Humanlapse .pageLayoutManualLayoutVideoPlayer {
      width: 100vw;
      height: calc(100vw); } }

