@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.Globals-noselect, .Home .button {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none; }
  .Globals-noselect:focus, .Home .button:focus, .Globals-noselect:active, .Home .button:active {
    outline: 0; }

.Globals-clickable:hover, .Home .button:hover {
  cursor: pointer; }

.Globals-clickable:focus, .Home .button:focus, .Globals-clickable:active, .Home .button:active {
  outline: 0; }

.Home .mainGrid {
  display: grid;
  align-items: stretch;
  justify-items: stretch;
  animation: all 0.5s ease-in-out;
  grid-template-rows: 50% 50%; }

.Home .message {
  width: calc(100% - 2rem);
  height: 100vh;
  display: grid;
  align-items: center;
  color: white;
  padding: 1rem; }
  .Home .message .text {
    text-align: center;
    font-size: 2.5em; }
    .Home .message .text.big {
      font-weight: 400;
      text-transform: uppercase; }
      @media (min-width: 960px) {
        .Home .message .text.big {
          margin-bottom: 0.5em; } }
    .Home .message .text.small {
      font-size: 1.5em;
      color: rgba(255, 255, 255, 0.7); }

.Home .button {
  display: grid;
  align-self: center;
  justify-self: center;
  align-items: center;
  height: 1.5em;
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  margin: 1em;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.7);
  text-align: center;
  text-decoration: none;
  font-size: 1.2em;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease; }
  @media (max-width: 599px) {
    .Home .button {
      width: 85%; } }
  @media (min-width: 600px) {
    .Home .button {
      width: 14em; } }
  .Home .button:hover {
    box-shadow: inset 0 0 0 1px white;
    color: white; }
  .Home .button:active {
    color: rgba(255, 255, 255, 0.7);
    box-shadow: inset 0 0 0 5px rgba(255, 255, 255, 0.7);
    -webkit-transition: all 0.03s ease;
    -moz-transition: all 0.03s ease;
    -ms-transition: all 0.03s ease;
    -o-transition: all 0.03s ease;
    transition: all 0.03s ease; }

.Home .fillPage {
  min-height: 100%; }
