@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.Globals-noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none; }
  .Globals-noselect:focus, .Globals-noselect:active {
    outline: 0; }

.Globals-clickable:hover {
  cursor: pointer; }

.Globals-clickable:focus, .Globals-clickable:active {
  outline: 0; }

.MenuPage .message {
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-right: 1rem;
  padding-left: 1rem;
