  display: grid;
  align-items: start;
  justify-items: center;
  color: white; }
  .MenuPage .message .text {
    text-align: center;
    font-size: 2.5em; }
    .MenuPage .message .text.big {
      font-weight: 400;
      text-transform: uppercase; }
      @media (min-width: 960px) {
